import React, {PropTypes} from 'react';
import {
    StyleSheet,
    View,
    Text,
    Button,
    TextInput, Dimensions,
    TouchableOpacity,
} from 'react-native';
import Colors from '../../common/Colors'
import StyleCommon from "../../common/StyleCommon";
import HomeNewPage from "../main/HomeNewPage";

let {height, width} = Dimensions.get('window');

export default class Login extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            borderColor: [],
            hasAccount: true,
            inputName: '',
            inputEmail: '',
            inputPwd: '',
            emailPhone: '',
            showError: false,
            errorTitle: '',
            errorContent: '',
            checkBox: false,
            showEmailSign: false,
            password:'',
        }
    }

    componentDidMount() {

    }

    componentWillUnmount() {

    }

    render() {
        return (
            <View style={styles.container}>
                <Text style={StyleCommon.black1_20}>Login</Text>

                <View style={{height: 20}}/>

                <TextInput
                    value={this.state.inputName}
                    placeholder='account'
                    onFocus={() => {
                        if (!this.state.showError) this.setState({borderColor: [0]})
                    }}
                    onBlur={() => {
                        if (this.state.showError) {
                            if ('' !== this.state.inputName) {
                                this.setState({
                                    borderColor: this.state.borderColor.splice(this.state.borderColor.indexOf(0), 1)
                                });
                                // console.warn(this.state.borderColor)
                            } else this.setState({borderColor: this.state.borderColor})
                        } else this.setState({borderColor: []})
                    }}
                    style={[StyleCommon.border_style, styles.textInput_view]}
                    keyboardType='default'
                    clearButtonMode='while-editing'
                    placeholderTextColor={Colors.hint}
                    onChangeText={(txt) => {
                        this.setState({inputName: txt})
                    }}
                />
                <TextInput
                    value={this.state.password}
                    placeholder='password'
                    secureTextEntry={!this.state.checkBox}
                    onFocus={() => {
                        if (!this.state.showError) this.setState({borderColor: [0]})
                    }}
                    onBlur={() => {
                        if (this.state.showError) {
                            if ('' !== this.state.password) {
                                this.setState({
                                    borderColor: this.state.borderColor.splice(this.state.borderColor.indexOf(0), 1)
                                });
                            } else this.setState({borderColor: this.state.borderColor})
                        } else this.setState({borderColor: []})
                    }}
                    style={[StyleCommon.border_style, styles.textInput_view]}
                    keyboardType='default'
                    clearButtonMode='while-editing'
                    placeholderTextColor={Colors.hint}
                    onChangeText={(txt) => {
                        this.setState({password: txt})
                    }}
                />
                <View style={{height: 20}}/>
                <TouchableOpacity
                    style={[styles.textInput_view,StyleCommon.column_center,{backgroundColor:Colors.theme_color,
                    borderRadius:10,height:60}]}
                    onPress={() => {
                        this.props.navigation.navigate("HomeNewPage");
                        // this.props.navigation.reset("HomeNewPage")
                    }}
                >
                    <Text style={StyleCommon.black1_20}>Login</Text>
                </TouchableOpacity>
            </View>
        );
    }
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "#F5FCFF"
    },
    contents: {
        marginHorizontal: 15,
        borderRadius: 3,
        backgroundColor: Colors.white,
        borderWidth: 0.5,
        borderColor: Colors.gray2,
        marginTop: 10
    },
    error_view: {
        marginBottom: 10,
        borderRadius: 3,
        paddingHorizontal: 8,
        borderColor: Colors.error,
    },
    error_title: {
        fontSize: 15,
        color: Colors.error,
        marginVertical: 5
    },
    radio_view: {
        height: 45,
        marginHorizontal: 10,
        justifyContent: 'center'
    },
    icon_color: {
        fontSize: 10,
        marginRight: 5,
        marginTop: 3,
        color: Colors.error,
    },
    textInput_view: {
        color: Colors.black2,
        height: 70,
        width: width / 2,
        paddingHorizontal: 20,
        marginTop:20,
        fontSize: 19
    },
    button: {
        borderWidth: 1,
        borderColor: "#000000",
        margin: 5,
        padding: 5,
        width: "70%",
        backgroundColor: "#DDDDDD",
        borderRadius: 5,
    },
});


