// App.js - WEB
import React, {Component} from "react";
import {View} from "react-native";
import WebRoutesGenerator from "./NativeWebRouteWrapper";
import {ModalContainer} from "react-router-modal";
import HomeScreen from "./example/HomeScreen";
// import TopNav from "./TopNav";
import SecondScreen from "./example/SecondScreen";
import UserScreen from "./example/UserScreen";
import DasModalScreen from "./example/DasModalScreen";
import Login from './pages/login/Login';
import HomeNewPage from "./pages/main/HomeNewPage";

const routeMap = {
    Login: {
        component: Login,
        path: "/",
        exact: true,
    },
    HomeNewPage: {
        component: HomeNewPage,
        path: "/HomeNewPage",
        exact: true
    },
    Home: {
        component: HomeScreen,
        path: "/home",
        exact: true
    },
    Second: {
        component: SecondScreen,
        path: "/home/second"
    },
    User: {
        component: UserScreen,
        path: "/home/user/:name?",
        exact: true
    },
    DasModal: {
        component: DasModalScreen,
        path: "*/dasmodal",
        modal: true
    }
};

class App extends Component {
    render() {
        return (
            <View style={{height: "100vh", width: "100vw"}}>
                {/*<TopNav />*/}
                {WebRoutesGenerator({routeMap})}
                <ModalContainer/>
            </View>
        );
    }
}

export default App;



