import React, {PropTypes} from 'react';
import {
    StyleSheet,
    View,
    Text,
    TouchableOpacity,
    Dimensions,

} from 'react-native';
import StyleCommon from "../../common/StyleCommon";
import Colors from "../../common/Colors";

let {height, width} = Dimensions.get('window');

export default class HomeNewPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {}
    }

    componentDidMount() {

    }

    componentWillUnmount() {

    }

    render() {
        return (
            <View style={[StyleCommon.container, {alignItems: 'center', marginTop: 50}]}>
                <Text style={StyleCommon.blue_20}>
                    HomePage
                </Text>

                <View style={[StyleCommon.row_wrap, {marginLeft: 20, marginTop: 20}]}>
                    {
                        [1, 2, 3, 4, 5, 6, 7].map((item, index) => {
                            return (
                                <TouchableOpacity
                                    key={index.toString()}
                                    style={styles.item_view}
                                    activeOpacity={0.8}
                                    onPress={() => {
                                        this.props.navigation.navigate('Second')
                                    }}
                                >
                                    <Text style={StyleCommon.black2_17}>
                                        {item}
                                    </Text>
                                </TouchableOpacity>
                            )
                        })
                    }
                </View>
            </View>
        );
    }
}

const styles = StyleSheet.create({

    item_view: {
        width: width / 3 - 30,
        height: 250,
        marginRight: 20,
        marginBottom: 20,
        backgroundColor: Colors.theme_color_transparent,
        justifyContent: 'center',
        alignItems: 'center'
    }
});


